<template>
  <div class="warpper">
    <div class="content" v-if="show">
      <i class="top"></i>
      <slot></slot>
      <div class="start-item" v-if="result.perf_score">
        
        <GradeItem :count="result.perf_score"></GradeItem>
      </div>
      <div class="start-item" v-if="result.skill_score">
        
        <GradeItem :count="result.skill_score"></GradeItem>
      </div>
      <div class="start-item ping">
        <template v-if="result.teacher_feedback">
          
          <p class="info">{{feedback}}</p>

          <span
            class="arrow"
            v-if="feedback.length >= 200"
            :class="{active: active}"
            v-show="show"
            @click="showHandler"
          >
            {{active ? "" : "" }}
            <i></i>
          </span>
        </template>
      </div>
      <i class="ip"></i>
    </div>
  </div>
</template>
<script>
import GradeItem from './GradeItem';
export default {
  name: "ReportPerformance",
  components: {
    GradeItem
  },
  data() {
    return {
      active: false
    }
  },
  props: {
    result: {
      type: Object,
      default: () => { }
    }
  },
  computed: {
    show() {
      if (this.result) {
        return this.result.perf_score || this.result.skill_score || this.result.teacher_feedback;
      } else {
        return false;
      }
    },
    feedback() {
      return this.active ? this.result.teacher_feedback : (this.result.teacher_feedback.substr(0, 200) + "...");
    }
  },
  methods: {
    showHandler() {
      this.active = !this.active;
    }
  }
}
</script>

