<template>
  <div class="warpper-item">
    <div class="video-view" v-if="url">
      <video :id="'video' + order" :src="url" ref="video" :order="order" @click="pauseHandler"></video>

      <div class="audio" v-if="result.audio_url && !result.video_url">
        <img :src="playing ? gif_playing : bg_playing">

        <p v-show="playing"></p>
        <p v-show="playing"></p>
      </div>

      <span class="buffer" v-if="loading">,....</span>

      <div class="control" v-show="!playing">
        <img v-if="result.pic_url" :src="result.pic_url">
        <button @click="playHandler"></button>
      </div>
    </div>

    <i class="header">&nbsp;&nbsp;&nbsp; {{order}}</i>
    <GradeItem class="grade" v-if="result.talk_score" :count="result.talk_score"></GradeItem>
    <div class="keyword" v-if="active">
      <template v-if="result.key_words && result.key_words.length">
        <p></p>
        <ul v-for="(text,index) in result.key_words" :key="index">
          <li>{{text}}</li>
        </ul>
      </template>

      <template v-if="result.key_sentences && result.key_sentences.length">
        <p></p>
        <ol v-for="(text, index) in result.key_sentences" :key="index">
          <li>{{text}}</li>
        </ol>
      </template>
    </div>
    <span class="info" :class="{active: active}" v-show="show" @click="showHandler">
      {{active ? "" : "" }}
      <i></i>
    </span>
  </div>
</template>
<script>
import GradeItem from './GradeItem';
import gif_playing from './../assets/image/music_playing.gif';
import bg_playing from './../assets/image/music_ip.png';

export default {
  name: "MomentItem",
  components: {
    GradeItem,
  },
  props: {
    order: {
      type: Number,
      default: 0
    },
    result: {
      type: Object,
      default: () => { }
    }
  },
  data() {
    return {
      gif_playing,
      bg_playing,
      active: false,
      loading: true,
      playing: false
    }
  },
  computed: {
    url() {
      if (this.result) {
        // return "https://h5player.bytedance.com/video/mp4/xgplayer-demo-480p.mp4";
        return this.result.video_url || this.result.audio_url;
      } else {
        return null;
      }
    },
    show() {
      if (this.result) {
        return this.result.key_words || this.result.key_sentences;
      } else {
        return false;
      }
    }
  },
  methods: {
    showHandler() {
      this.active = !this.active;
    },
    playHandler() {
      this.$refs['video'].play();

      const that = this;
      const list = document.querySelectorAll('video');
      list.forEach(video => {
        if (video.getAttribute('order') != that.order) {
          video.pause();
        }
      });
    },
    pauseHandler() {
      this.$refs['video'].pause();
    }
  },
  mounted() {
    const video = this.$refs['video'];
    video.oncanplay = () => {
      this.loading = false;
    };

    video.onwaiting = () => {
      this.loading = true;
    };

    video.onplaying = () => {
      this.playing = true;
    };

    video.onpause = () => {
      this.playing = false;
    };

    video.onended = () => {
      this.playing = false;
    };
  }
}
</script>

