<template>
  <div class="warpper" v-if="show">
    <div class="content">
      <slot></slot>
      <div class="start-item" v-if="result.vocabulary && result.vocabulary.length">
        <span></span>
        <div class="keyword">
          <p v-for="(item, index) in result.vocabulary" :key="index">{{item}}</p>
        </div>
      </div>
      <div class="start-item" v-if="result.sentence && result.sentence.length">
        <span></span>
        <div class="sentence">
          <p v-for="(item, index) in result.sentence" :key="index">{{item}}</p>
        </div>
      </div>
      <div class="start-item grammar" v-if="result.grammar && result.grammar.length">
        <span></span>
        <div class="grammar">
          <p v-for="(item, index) in result.grammar" :key="index">{{item}}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import GradeItem from './GradeItem';
export default {
  name: "ReportPerformance",
  components: {
    GradeItem
  },
  props: {
    result: {
      type: Object,
      default: () => { }
    }
  },
  computed: {
    show() {
      if (this.result) {
        return this.result.vocabulary && this.result.vocabulary.length > 0 ||
          this.result.sentence && this.result.sentence.length > 0 ||
          this.result.grammar && this.result.grammar.length > 0;
      } else {
        return false;
      }
    }
  }
}
</script>

