<template>
  <div class="warpper">
    <div class="box">
      <a class="bang">
        <img :src="nebula">
        {{level.label}}
      </a>
    </div>
    <div class="box info">{{level.info}}</div>
    <div class="box dia">
      <span class="top">
        
        <b>{{result.diamond || 0}}</b>
      </span>
      <span class="bottom">
        
        <i></i>
        <b>{{result.diamond_total || 0}}</b>
      </span>
    </div>

    <div class="box qrcode">
      <img class="left" :src="user_icon_1">
      <img class="right" :src="user_icon_2">

      <div class="qrbox">
        <img class="qr" :src="qrcode">
        <b></b>
      </div>
    </div>
  </div>
</template>
<script>
import user_icon_1 from './../assets/image/icon_01.png';
import user_icon_2 from './../assets/image/icon_02.png';
import qrcode_bg from './../assets/image/qrcode.png';
import nebula from "./../assets/image/nebula.png";
export default {
  name: 'ReportUser',
  data() {
    return {
      user_icon_1,
      user_icon_2,
      nebula,
      qrcode_bg
    };
  },
  props: {
    result: {
      type: Object,
      default: () => { }
    }
  },
  computed: {
    level() {
      const st = this.result && this.result.stu_level || null;
      let list = [];
      if (st) {
        list = st.split('#');
      }
      return {
        label: list[0],
        info: list[1]
      };
    },
    qrcode() {
      if (this.result.qrcode) {
        return "data:image/png;base64," + this.result.qrcode;
      } else {
        return this.qrcode_bg;
      }
    }
  },
}
</script>
