<template>
  <div class="warpper" v-if="show">
    <div class="content">
      <slot></slot>
      <div class="start-item" v-if="result.stu_attendance">
        <span></span>
        <p>{{attendance}}</p>
      </div>
      <div class="start-item" v-if="result.stu_activity">
        <span></span>
        <p>{{result.stu_activity}}</p>
      </div>
    </div>
  </div>
</template>
<script>
import GradeItem from './GradeItem';
import { getLocalTime, getLocalZone } from '@/plugins/Clock.js'
export default {
  name: "ReportPerformance",
  components: {
    GradeItem
  },
  props: {
    result: {
      type: Object,
      default: () => { }
    }
  },
  computed: {
    show() {
      if (this.result) {
        return this.result.stu_attendance || this.result.stu_activity;
      } else {
        return false;
      }
    },
    attendance() {
      function fillStr(i) {
        return i >= 10 ? i : '0' + i;
      }

      var date = getLocalTime(-getLocalZone(), this.result.enter_time_format);
      var time = fillStr(date.getHours()) + ":" + fillStr(date.getMinutes()) + ":" + fillStr(date.getSeconds()) ;
      var str = this.result.stu_attendance || "";
      return str.replace('%s', time);
    }
  }
}
</script>

