<template>
  <div class="warpper">
    <div class="content">
      <slot></slot>
      <div class="start-item">
        <span class="title">{{result.courseware_name}}</span>
        <ul>
          <li>
            
            <b>{{result.classtype_name}}</b>
          </li>
          <li>
            
            <b>{{time}}</b>
          </li>
          <li>
            
            <b>{{result.lesson_duration}}</b>
          </li>
          <li>
            
            <b>{{result.teacher_name}}</b>
          </li>
        </ul>
        <!-- <p> level2</p> -->
      </div>
      <div class="start-item prevew">
        <b>{{result.teacher_name}}</b>
        <p>{{result.teacher_expr_years}}</p>
        <p>{{result.teacher_certs}}</p>
        <img :src="result.teacher_avatar" alt="">
      </div>
      <div class="start-item">
        <p>{{result.teacher_introduction}}</p>
      </div>
    </div>
  </div>
</template>
<script>
import GradeItem from './GradeItem';
import { getLocalTime, getLocalZone } from '@/plugins/Clock.js'
export default {
  name: "ReportPerformance",
  components: {
    GradeItem
  },
  props: {
    result: {
      type: Object,
      default: () => { }
    }
  },
  computed: {
    time() {
      function fillStr(i) {
        return i >= 10 ? i : '0' + i;
      }
      
      var date = getLocalTime(-getLocalZone(), this.result.start_time_format);
      return fillStr(date.getMonth() + 1) + "/" + fillStr(date.getDate()) +
        " " + fillStr(date.getHours()) + ":" + fillStr(date.getMinutes());
    }
  }
}
</script>

