<template>
  <div class="wrapper">
    <img :src="bg" class="title">
    <EmptyReport v-if="empty" class="empty" :loading="loading"></EmptyReport>

    <template v-else>
      <ReportHeader :result="reportResult" class="title"></ReportHeader>
      <ReportUser :result="reportResult"></ReportUser>
      <ReportComments :result="reportResult">
        <ReportItemHeader title=""></ReportItemHeader>
      </ReportComments>
      <ReportMoment :result="reportResult">
        <ReportItemHeader title=""></ReportItemHeader>
      </ReportMoment>
      <ReportPerformance :result="reportResult">
        <ReportItemHeader title=""></ReportItemHeader>
      </ReportPerformance>
      <ReportCourse :result="reportResult">
        <ReportItemHeader title=""></ReportItemHeader>
      </ReportCourse>
      <ReportSummary :result="reportResult">
        <ReportItemHeader title=""></ReportItemHeader>
      </ReportSummary>
    </template>

    <div class="footer"></div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import ReportItemHeader from './components/ReportItemHeader';
import ReportHeader from './components/ReportHeader.vue';
import EmptyReport from './components/EmptyReport.vue';
import ReportUser from './components/ReportUser.vue';
import ReportMoment from './components/ReportMoment.vue';
import ReportComments from './components/ReportComment.vue';
import ReportPerformance from './components/ReportPerformance.vue';
import ReportCourse from './components/ReportCourse.vue';
import ReportSummary from './components/ReportSummary.vue';

import bg from "./assets/image/report_title.png";
export default {
  name: "App",
  components: {
    ReportItemHeader,
    ReportHeader,
    EmptyReport,
    ReportUser,
    ReportComments,
    ReportMoment,
    ReportPerformance,
    ReportCourse,
    ReportSummary,
  },
  data() {
    return {
      bg,
      loading: true,
      reportResult: {},
    };
  },
  computed: {
    empty() {
      return Object.keys(this.reportResult).length == 0;
    }
  },
  methods: {
    onResize(element) {
      var w = element.offsetWidth;
      var width = element.parentNode.offsetWidth;
      var r = width / w;
      r = r > 2 ? 2 : r < 1 ? 1 : r;
      element.style.zoom = r;
    },
    reportHandler({ isSuccess, code, message, result }) {
      if (isSuccess && result) {
        this.loading = false;
        this.reportResult = result;
      }
    },
    reportError(err) {
      console.error(err);
    }
  },
  mounted() {
    this.onResize(document.querySelector('body>.wrapper'));
    window.onresize = () => {
      this.onResize(document.querySelector('body>.wrapper'));
    };

    //ID
    const reg = new RegExp("\\/report\\/([a-zA-Z0-9-_]+)\\/", "gi");
    const result = reg.exec(window.location.href);
    if (result) {
      this.checked = true;
      console.log(result[1]);
      this.Api.getReport(result[1]).then(this.reportHandler).catch(this.reportError);
    } else {
      this.title = "";
    }
  }
}
</script>

