import BaseApiService from '@/service/BaseApiService'

class Api extends BaseApiService {
    constructor() {
        super();
    }

    /**
     * 
     */
    getReport = (id) => this.request('report/load', { report_id: id });

    getQrcode = (phone, width) => this.request('report/qrcode', { phone, width });
}

export default {
    install(Vue) {
        Vue.prototype.Api = new Api();
        Vue.prototype.$api = Vue.prototype.Api;
    }
}